<template>
    <div id="mainmain">
      <div id="head">
        <div class="logo">
         <img src="./../assets/logo.png">
         <span>狗敦A季</span>
        </div>
         <div class="inp">
          <el-input placeholder="请输入电影名、或网址" class="inpu"  v-model="key">
             <el-button class="inpb"  v-on:click="searchM" slot="append" icon="el-icon-search">搜索</el-button>
          </el-input>
        </div>
      </div>
      <ul class="mList">
        <li v-for="li in list">
          <div class="l">
            <img :src="li.imgurl" >
          </div>
          <div class="r">
            <p class="p1">
              <span class="s1">{{li.name}}</span>
              <span class="s2">{{li.year}}</span>
            </p>
            <p v-if="li.director"  class="p2">
              <span  class="s1">导演：</span>
              <span class="s2">{{li.director}}</span>
            </p>
             <p  v-if="li.stars"   class="p3">
              <span  class="s1">主演：</span>
              <span class="s2" v-for="sta in li.stars">{{sta }}  &nbsp;&nbsp;</span>
            </p>
            <p  v-if="li.time"  class="p4">
              <span  class="s1">时间：</span>
              <span class="s2">{{li.time}}</span>
            </p>
            <p  v-if="li.area"  class="p5"> 
              <span  class="s1">地区：</span>
              <span class="s2">{{li.area}}</span>
            </p>
             <p class="p6">
              <span  class="s1">简介：</span>
              <span class="s2">{{li.introdu | splitIntru}}</span>
            </p>
            <p class="play">
              <el-button type="primary" v-on:click="showM(li.url,li.name)" size="mini">播放</el-button>
            </p>
          </div>
        </li>
      </ul>
    </div>
</template>

<script>
import { searchList } from "../api/api.js";
export default {
  filters: {
    splitIntru(val) {
      if (val.length > 90) {
        return val.substr(0, 90) + "...";
      } else {
        return val;
      }
    }
  },
  activated() {
    this.getM();
  },
  mounted() {
    this.getM();
  },
  data() {
    return {
      key: "战狼",
      list: []
    };
  },
  methods: {
    searchM() {
      if (this.key.trim() == "") {
        this.$notify({
          title: "狗敦A季提醒您",
          message: "请输入电影名或电影地址",
          type: "warning"
        });
      } else {
        if (
          this.key.trim().indexOf("http://") > -1 ||
          this.key.trim().indexOf("https://") > -1
        ) {
          this.$router.push({
            name: "movie",
            params: {
              url: this.key
            }
          });
        } else {
          searchList(this.key).then(res => {
            this.list = res.data.data;
            if (this.list.length < 1) {
              this.$notify({
                title: "狗敦A季提醒您",
                message: "暂未搜索到相关电影！",
                type: "warning"
              });
            }
          });
        }
      }
    },
    showM(url, name) {
      this.$router.push({
        name: "movie",
        params: {
          url: url,
          name: name
        }
      });
    },
    getM() {
      if (this.$route.params.title) {
        var tit = this.$route.params.title;
        this.key = tit;
        searchList(tit).then(res => {
          this.list = res.data.data;
        });
      } else {
        // alert("直接打开的");
      }
    }
  }
};
</script>

<style lang="less">
.el-input-group__append {
  background: #2196f3 !important;
  color: #fff;
  border: 1px solid #2196f3;
  cursor: pointer;
  letter-spacing: 5px;
}
#mainmain {
  #head {
    background: #fff;
    width: 100%;
    height: 60px;
    box-shadow: 0 0 3px #888;
    position: fixed;
    z-index: 1000;
    .logo {
      width: 125px;
      height: 60px;
      line-height: 60px;
      color: #444141;
      cursor: pointer;
      float: left;
      img {
        padding: 10px;
        width: 40px;
        height: 40px;
        vertical-align: middle;
        padding-right: 0;
      }
    }
    .inp {
      width: 50%;
      display: inline-block;
      line-height: 60px;
      input {
        height: 38px;
      }
      .inpu {
        border: 1px solid #2196f3 !important;
        border-radius: 3px;
      }
    }
  }
  .mList {
    position: absolute;
    top: 70px;
    z-index: 1;
    width: 70%;
    margin-left: 125px;
    li {
      height: 180px;
      list-style: none;
      margin: 10px 0;
      padding-bottom: 10px;
      border-bottom: 1px solid #eee;
      position: relative;
      .l {
        height: 180px;
        width: 140px;
        float: left;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .r {
        float: left;
        width: 70%;
        height: 150px;
        margin-left: 15px;
        .p1 {
          .s1 {
            font-size: 16px;
            color: #f15a24;
          }
          .s2 {
            color: #999;
            font-size: 12px;
            margin-left: 5px;
          }
        }
        .p2,
        .p3,
        .p4,
        .p5,
        .p6 {
          font-size: 12px;
          line-height: 20px;
          .s1 {
            color: #999;
          }
          .s2 {
          }
        }
        .play {
          position: absolute;
          bottom: 10px;
          button {
            padding: 5px 15px;
            cursor: pointer;
          }
        }
      }
      @media (min-width: 1500px) {
        .r {
          width: 65%;
        }
      }
    }
  }
}
</style>



